@media (orientation: portrait) {

    html {
        overflow-x: hidden;
        /* background-color: black; */
    }

    body {
        width: 100%;
        /* background-color: white; */
    }

  .carols {
        width: 100vw;
        object-fit: cover;
        height: 60vh;
        object-position: 50% 0%;
        overflow: hidden;
    }

    .menu {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 60vh;
        width: 100vw;
        z-index: 4;
        position: absolute;
        background: linear-gradient(to right, rgba(0,0,0,0), rgba(0,0,0,1), rgba(0,0,0,0));
    }


    .menuItems {
        color: white;
        font-size: 20vw;
        font-weight: 800;
        padding: 0;
        margin: 0;
        cursor: pointer;
        transition: 0.5s;
    }

    .inkless_section {
        margin-top: 10vh;
        width: 80vw;
        margin-left: 10vw;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 4vh;
        flex-flow: column-reverse;
        flex-direction: column-reverse;
    }

    .leftFlex {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 2vh;
    }

    .rightFlex {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        /* scale: 2; */
        overflow-x: hidden;
    }

    svg {
        width: 110%;
        height: auto;
    }

    h2 {
        font-size: 15vw;
        width: 100%;
        padding: 0;
        margin: 0;
        text-align: center;
    }

    .blueButton {
        background: #007aff;
        height: 8vh;
        font-size: 3vh;
        color: white;
        outline: none;
        border-radius: 4vh;
        border: none;
        width: 80vw;
        font-family: "Inter";
        display: inline-block;
        transition: 0.3s;
        cursor: pointer;
    }

    .blueButton:focus {
        background: #2e93ff;
        scale: 1.02;
        transition: 0.3s;
    }

    .cls-1 {
        fill: none;
        stroke: #000;
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-width: 8px;
    }
}