/* Fonts */

@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');



html, body {
    overscroll-behavior: none;
    margin-top: 10vh;
    font-family: 'Inter';
    width: 100vw;
}


/* Navbar */


.barGod {
    top: 0;
    position: fixed;
    z-index: 10;
}

.bar {
    z-index: 100;
    width: 100vw;
    /* top: 0; */
    /* position: fixed; */
    height: 20vh;
    background-color: rgba(255, 255, 255, 1);
    /* box-shadow: 1vh 1vh 5vh rgba(255,255,255,0.4); */
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2vw;
    overflow-y: none;
    backdrop-filter: blur(1vh);
}

.burger {
    left: 0;
    position: absolute;
    scale: 2;
}

.menuOptions {
    background-color: black;
}

h1 {
    font-family: "Inter";
    font-weight: bold;
    font-size: 7.5vh;
    color: white;
    padding: 0;
    margin: 0;
    height: 100%;
    user-select: none;
    line-height: 8.5vh;
}

.blackTile {
    background-color: black;
    width: 10vh;
    height: 10vh;
    border-radius: 2vh;
}

.tile {
    width: 10vh;
    height: 10vh;
    border-radius: 2vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbarItems {
    color: black;
    font-size: 3vh;
    font-family: "Inter";
}




/* Inkless */

.inkless_section {
    margin-top: 10vh;
    width: 80vw;
    margin-left: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    gap: 7vh;
}

.leftFlex {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 2vh;
}

.rightFlex {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

h2 {
    font-size: 10vh;
    width: 100%;
    padding: 0;
    margin: 0;
}

.blueButton {
    background: #007aff;
    height: 8vh;
    font-size: 3vh;
    color: white;
    outline: none;
    border-radius: 4vh;
    border: none;
    width: 25vw;
    font-family: "Inter";
    display: inline-block;
    transition: 0.3s;
    cursor: pointer;
}

.blueButton:hover {
    background: #2e93ff;
    scale: 1.02;
    transition: 0.3s;
}

.cls-1 {
    fill: none;
    stroke: #000;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-width: 8px;
}

/* Menu */

.menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    height: 80vh;
    width: 100vw;
    z-index: 4;
    position: absolute;
    background: linear-gradient(to right, rgba(0,0,0,1), rgba(0,0,0,0));
}


.menuItems {
    color: white;
    font-size: 7vw;
    font-weight: 800;
    padding: 0;
    margin: 0;
    cursor: pointer;
    transition: 0.5s;
        margin-left: 5vw;
}

.menuItems:hover {
    transition: 1s;
    font-weight: 1000;
}

/* Photo */

.carols {
    width: 100vw;
    object-fit: cover;
    height: 80vh;
    object-position: 100% 0%;
}